@mixin ab($t, $l, $b, $r) {
    position: absolute;    
    @if $l != n { left: $l; }
    @if $t != n { top: $t; }
    @if $r != n { right: $r; }
    @if $b != n { bottom: $b; }
}
@mixin fixed($t, $l, $b, $r) {
    position: fixed;
    @if $l != n { left: $l; }
    @if $t != n { top: $t; }
    @if $r != n { right: $r; }
    @if $b != n { bottom: $b; }    
}
@mixin transform($transforms) {	
	transform: $transforms;
}
@mixin rotate($rotate) { 
  transform: rotate($rotate);
}
@mixin translateX($x) {
    @include transform(translateX($x));
}
@mixin translateY($y) {
    @include transform(translateY($y));
}
@mixin translate($x, $y) {  
    @include transform(translate($x, $y));
}
@mixin placeholder {
    &::-webkit-input-placeholder {@content}
    &:-moz-placeholder           {@content}
    &::-moz-placeholder          {@content}
    &:-ms-input-placeholder      {@content}
}
@mixin border-inline-start($value) {
    border-inline-start:$value;
}  
@mixin border-inline-end($value) {
    border-inline-end:$value;
}
@mixin padding-start($value) {
    padding-inline-start: $value;
}
@mixin padding-end($value) {
    padding-inline-end: $value;
}
@mixin margin-start($value) {
    margin-inline-start: $value;
}
@mixin margin-end($value) {
    margin-inline-end: $value;
}
@mixin clamp($bb) {
  display: -webkit-box;
  -webkit-line-clamp: $bb;
  -webkit-box-orient: vertical;
  overflow: hidden;
}
@mixin border-radius($tl, $tr, $br, $bl) {
  border-radius: $tl $tr $br $bl;
}
@mixin padding($pt, $pr, $pb, $pl) {
  padding: $pt $pr $pb $pl;
}
@mixin left($l) {
  left: $l;
}
@mixin right($r) {
  right: $r;
}
@mixin text-right {
    text-align: end;
}
@mixin text-left {
    text-align: start;
}

@mixin flex-end {
    justify-content: flex-end;
}
@mixin flex-start {
    justify-content: flex-start;
}


%list-style-reset {
    list-style: none;
}
%point-event {
    pointer-events: none;     
}
%point-cursor {
    cursor: pointer;
}
%absolute-content {                   
    display: table;
    width: -moz-fit-content;
    width: fit-content;  
}
%visibility-hidden {
    visibility: hidden;
    z-index: -2;
    opacity: 0;
}
%visibility-visible {
    visibility: visible;
    z-index: 99992;
    opacity: 1;
}
%transition {
    transition: all 0.2s ease-in-out 0s;
}
%fw-custom-1 {
    font-weight: 500;
} 
%radius-4px {
    border-radius: 4px;
}
%radius-circle {
    border-radius: 50%;
}
%radius-reset {
    border-radius: 0;
}
%box-shadow {
    box-shadow: 0px 0px 24px rgba(150, 151, 151, 0.1);
}
%section-heading-cl {
    color: var(--section-heading-cl);
}
%webkit-scrollbar-track {
    box-shadow: inset 0 0 4px var(--body-border-color);
    -webkit-box-shadow: inset 0 0 var(--body-border-color);
    background-color: var(--body-border-color);
    border-radius: 5px;
}
%webkit-scrollbar {
    width: 4px;
    background-color: var(--body-border-color);
    border-radius: 5px;
}
%webkit-scrollbar-thumb {
    background-color: var(--main-color);
    border-radius: 5px;
}
%scroll-bar {
    scrollbar-color: var(--main-color) var(--body-border-color);
    scrollbar-width: thin; 
    position: relative;        
}
@mixin effect-img {
    opacity: 1;
    transition: opacity .35s,transform .35s;
    transform: translate3d(-18px,0,0) scale(1.1);
    backface-visibility: hidden;
}
@mixin hover-effect-img {
    opacity: 1;
    transform: translate3d(0,0,0) scale(1);
}

@-webkit-keyframes animation-top-bottom {
  0% {
    opacity: 0;
    -webkit-transform: translate(0, -30px);
            transform: translate(0, -30px);
  }
  100% {
    opacity: 1;
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
  }
}

@keyframes animation-top-bottom {
  0% {
    opacity: 0;
    -webkit-transform: translate(0, -30px);
            transform: translate(0, -30px);
  }
  100% {
    opacity: 1;
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
  }
}

@-webkit-keyframes animation-bottom-top {
  0% {
    opacity: 0;
    -webkit-transform: translateY(60px);
            transform: translateY(60px);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@keyframes animation-bottom-top {
  0% {
    opacity: 0;
    -webkit-transform: translateY(60px);
            transform: translateY(60px);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}
@keyframes shake {
  10%, 90% {
    transform: translate3d(-1px, 0, 0);
  }
  
  20%, 80% {
    transform: translate3d(2px, 0, 0);
  }

  30%, 50%, 70% {
    transform: translate3d(-3px, 0, 0);
  }

  40%, 60% {
    transform: translate3d(3px, 0, 0);
  }
}
@-webkit-keyframes move {
  0% {
    background-position: 0 0;
  }
  100% {
    background-position: 40px 0;
  }
}
@-moz-keyframes move {
  0% {
    background-position: 0 0;
  }
  100% {
    background-position: 40px 0;
  }
}
@-ms-keyframes move {
  0% {
    background-position: 0 0;
  }
  100% {
    background-position: 40px 0;
  }
}
@-o-keyframes move {
  0% {
    background-position: 0 0;
  }
  100% {
    background-position: 40px 0;
  }
}
@keyframes move {
  0% {
    background-position: 0 0;
  }
  100% {
    background-position: 40px 0;
  }
}
%animation-active {
    @for $index from 2 through 20 {
        li:nth-child(#{$index}) {
          animation-delay: 0.1s * $index;
        }
    }
}

/*manage z-index*/ 
@function layer($name) {
@if map-has-key($layer, $name) {
@return map-get($layer, $name);
}
@warn "The key #{$name} is not in the map ’$layer’";
@return null;
};
