.footer {
	background: #b5b5b5;	
	.main__text {
		text-transform: uppercase;
	}
	.main__text-2 {
		text-transform: inherit;
	}
	.card_footer-item {
		margin-bottom: 30px;
	}
	.card_footer-logo {
		margin-bottom: var(--gutter-lg);
		.svg-footer {
			font-size: 0;
			svg {
				height: 45px;
			}
		}
	}
	.card_footer-title {
		margin-bottom: var(--gutter-sm);
		display: flex;
		align-items: center;
		justify-content: space-between;
		svg {
			width: 14px;
			height: 14px;
		}
		.collapse {
			width: 40px;
			height: 20px;
			@include text-right;
		}
	}
	.card_footer-text {
		display: flex;
		align-items: flex-start;
		line-height: 1.5;
		margin-bottom: 15px;
		span {
			line-height: 1.6;
		}
		p {
			margin: 0;
		}
		.svg_element-ui {
			@include margin-end(var(--gutter-xs));
			margin-top: 2px;
			svg {
				width: 14px;
				height: 14px;
			}
		}
	}
	.form-subscribe {
		margin-top: 20px;
		display: flex;
		input {
			@extend %radius-reset;
			border-inline-end: 0;
		}
		.btn {
			padding: 0;
			display: block;
			max-width: 50px;
			width: 100%;
			@extend %radius-reset;
		}
		.btn-text {
			display: flex;
			align-items: center;
			justify-content: center;
			svg {
				width: 20px;
				height: 20px;
			}
		}
	}
	.card_footer-social {
		margin-top: 50px;
		margin-bottom: 20px;
		.title {
			text-transform: uppercase;
			margin-bottom: 15px;
			display: block;
		}
	}
	.card_footer-menu {
		.menu_list-wrapper {
			transition: 0.2s all ease-out;
			display: none;
			overflow: hidden;
			@media (min-width: 768px) {
				display: block;
			}
			li {
				padding: 5px 0;
				line-height: 22px;
				font-size: var(--body-font-size);
				position: relative;
			}
			&.is-open {
				display: block;
			}
		}
	}
	.list-style {
		li {
			@include padding-start(15px);
			&:before {
				content: "";
				width: 4px;
				height: 4px;
				background: var(--main-color);
				@include ab(14px,0,n,n);
			}
			a {
				@include padding-start(15px);
			}
		}
	}
	.accept-payment {
		margin-top: 60px;
		.title {
			margin-bottom: 15px;
			display: block;
		}
		.payment-list {
			display: flex;
			>span {
				width: 55px;
				@include margin-end(10px);
				margin-bottom: 10px;
			}
		}
	}
	@media (min-width: 768px) {
		.card_footer-item {
			margin-bottom: 50px;
		}
	}
}
.section-footer {
	.copyright-content {
		font-size: calc(var(--body-font-size) - 2px);
		padding: 12px 0;
		text-align: center;
		@media (min-width: 992px) {
			@include text-left;
		}
		p {
			margin: 0;
			letter-spacing: 0.4px;
			line-height: 1.7;
			font-weight: 300;
		}
	}
}